import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs';
import { AngularFirestore } from 'angularfire2/firestore';
import { Collections, Content } from '../../app/app.models';
import { map } from 'rxjs/operators';

@IonicPage()
@Component({
  selector: 'page-especialidades',
  templateUrl: 'especialidades.html',
})
export class EspecialidadesPage {

  public materias$: Observable<Content[]>
  public isPrograma: boolean = this.navParams.get('isPrograma');

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    private afs: AngularFirestore
  ) {
  }

  ionViewDidLoad() {
    /* this.back.getAllMaterias().subscribe(data => this.materias = data.filter(m => m.name.includes('Temprano')), err => this.toast.create({message: 'No se pudieron cargar las materias...', duration: 2000}).present()) */
    this.materias$ = this.afs.collection<Content>(Collections.CONTENT)
      .valueChanges()
      .pipe(
        map(materias => materias.filter(m => m.name.includes('Temprano')))
      )
  }

  openMateria(id: string) {
    this.navCtrl.push(this.isPrograma ? 'ProgramaMateriaPage' : 'MateriaPage', {id})
  }

}



